// 可用区管理
import service from "../http"
import { getEcsOptions }from '../common'

export default{
    //获取可用区列表
    get_az_list(req){
        return service(getEcsOptions('GET','/ecs/manage_az_list/',req))
    },
    //编辑可用区
    edit(req){
        return service(getEcsOptions('POST','/ecs/az_edit/',req))
    },
    //同步可用区信息
    sync(){
        return service(getEcsOptions('GET','/ecs/sync_az_info/'))
    },
    //获取可用区存储类型
    get_backend_types(){
        return service(getEcsOptions('GET','/ecs/get_backend_types/'))
    },
    //获取可用区状态
    get_status(){
        return service(getEcsOptions('GET','/ecs/get_az_status_list/'))
    },
    //删除可用区
    del_status(req){
        return service(getEcsOptions('POST','/ecs/delete_az/',req))
    },
    //获取可用区操作记录
    record(req){
        return service(getEcsOptions('GET','/ecs/get_az_history/',req))
    },
    //获取可用区网络类型
    get_net_type(){
        return service(getEcsOptions('GET','/ecs/get_az_net_types/'))
    },
    //获取可用区资源调度参数
    get_scheduling_policy(req){
        return service(getEcsOptions('GET','/ecs/get_scheduling_policy/',req))
    },
    //获取可用区资源调度参数
    set_scheduling_policy(req){
        return service(getEcsOptions('POST','/ecs/set_scheduling_policy/',req))
    },
    enable_cluster_schedule(req){
        return service(getEcsOptions('POST','/ecs/enable_cluster_schedule/',req))
    },
    //获取镜像卷列表
    get_image_disk_list(req){
        return service(getEcsOptions('POST','/img/get_image_disk_list/',req))
    },
    //设置镜像卷调度策略
    set_image_disk_strategy(req){
        return service(getEcsOptions('POST','/img/set_image_disk_strategy/',req))
    },
    //获取回滚时快照信息
    get_rollback_info(req){
        return service(getEcsOptions('POST','/img/get_rollback_snapshot_dict/',req))
    },
    //回滚镜像卷
    rollback_image_disk(req){
        return service(getEcsOptions('POST','/img/rollback_image_disk/',req))
    }
}